.window-scrollbar ::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.window-scrollbar ::-webkit-scrollbar-track {
  width: 6px;
  background: rgba(16, 31, 28, 0.1);
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.window-scrollbar ::-webkit-scrollbar-thumb {
  background-color: rgba(16, 31, 28, 0.5);
  background-clip: padding-box;
  min-height: 28px;
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.window-scrollbar ::-webkit-scrollbar-thumb:hover {
  background-color: #101f1c;
}
